import React from "react";
import classNames from "classnames";
import * as style from "../assets/scss/index.module.scss";
import Circle from "react-circle";

export const SkillDashboard: React.FunctionComponent<any> = React.memo(({skills}: any): any => {
    return <div className={classNames(style.dFlex, style.flexWrap, style.textCenter)}>
        {
            skills.map((item: any, index: any) => {
                return <div className={classNames(style.gMr20, style.gMb20, style.gMb0Xl)} key={index}>
                    <Circle
                        animate={true}
                        responsive={false}
                        size="70"
                        lineWidth="15"
                        progress={item.value}
                        progressColor={item.color.front}
                        bgColor={item.color.back}
                        textColor={item.color.text}
                        textStyle={{
                            position: "absolute",
                            top: "0px",
                            left: "0px",
                            textAlign: "center",
                            width: "100%",
                            fontSize: "80px",
                            height: "60px",
                            lineHeight: "60px"
                        }}
                        percentSpacing={10}
                        roundedStroke={true}
                        showPercentage={true}
                        showPercentageSymbol={true}
                    />
                    <h6 className={classNames(style.h6, style.gFontWeight300)}>
                        {item.title}
                    </h6>
                </div>
            })
        }
    </div>
})